import { useParams, Link } from "react-router-dom";
import { useState, useEffect } from "react";
import api from '../api'
import Fiche from '../components/Fiche'

const Edit = () => {
  const { rowIndex } = useParams();
  const [data, setData] = useState(api.defaultState);
  const [lists, setList] = useState(api.defaultList);
  const [bookCount, setBookCount] = useState(0);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    const getData = async () => {
      setData(await api.getBook(rowIndex))
    };
    const getList = async (ame) => setList(await api.getLists())
    const getBookCount = async () => setBookCount(await api.getBookCount())
    getData();
    getBookCount();
    getList();
  }, [rowIndex]);

  const handleChange = (e) =>
    setData({ ...data, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    setSaving(true)
    //e.preventDefault();
    await api.saveBook(rowIndex, data)
    //history.replace("/");
    setSaving(false)
  };

  return (
    <div style={{ margin: "auto", position: 'relative' }} >

      <Fiche props={{ data: data, lists: lists, handleChange }} />
      <div className="row"><p>pied de page</p></div>

      {!saving ? (
        <div className="row fixed-bottom bg-light p-3">
          <div className="col">
            <Link
              to={`/edit/${parseInt(rowIndex) - 1}`}
              className={`btn btn-primary ${rowIndex === "0" ? "disabled" : ""}`}>
              &lt;&lt;
            </Link>
          </div>

          <div className="col text-center">
            <button className="float-center btn btn-primary" onClick={handleSubmit}>Enregistrer</button>
          </div>

          <div className="col">
            <Link
              to={`/edit/${parseInt(rowIndex) + 1}`}
              className={`float-end btn btn-primary ${parseInt(rowIndex) + 1 === bookCount ? "disabled" : ""}`}>
              &gt;&gt;
            </Link>
          </div>
        </div>) : (<div className="bg-dark-transparent loading-panel"></div>)}

    </div>
  );
};

export default Edit;
