import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import api from '../api'
import Fiche from '../components/Fiche'

const Add = () => {
  const history = useHistory();
  const [data, setData] = useState(api.defaultState);
  const [lists, setList] = useState(api.defaultList);

  useEffect(() => {
    const getList = async () => setList(await api.getLists())
    getList();
  }, []);

  const handleChange = (e) =>
    setData({ ...data, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    await api.addBook(data)
    history.replace("/");
  }

  return (
    <form style={{ margin: "auto" }} onSubmit={handleSubmit}>

      <Fiche props={{data, lists, handleChange}} />

      <div className="row"><p>pied de page</p></div>

      <div className="text-center fixed-bottom row">
        <button className="btn btn-primary col align-self-end">Enregistrer</button>
      </div>
    </form>
  );
};

export default Add;
