const defaultState = {
  Name: "",
  Author: "",
  Description: "",
  Jacket: '',
  Genre: '',
  SousGenre: '',
  Statut: 'Non lu',
  Format: '',
  Tome: '',
  Commentaire: '',
  NbPages: '',
  LentTo: '',
  LentOn: '',
  LentReturnOn: '',
  Rating: '0',
  EditionYear: '',
}

const defaultList = {
  genres: [],
  sousGenres: [],
  statuts: [],
  formats: [],
}

const api = () => {

  const doc = new GoogleSpreadsheet(config.SPREADSHEET_ID);

  let initialised = false;
  let bookSheet = null;
  let books = null;
  let lists = {};

  const init = async () => {
    if (!initialised) {
      console.log("Initialisation de l'api")
      await doc.useServiceAccountAuth({
        client_email: config.CLIENT_EMAIL,
        private_key: config.PRIVATE_KEY,
      })

      await doc.loadInfo();

      bookSheet = doc.sheetsById[config.SHEET_ID];
      books = await bookSheet.getRows();
      console.log('book count=' + books.length)

      const sheet = doc.sheetsById[config.SHEET_LIST_ID]
      const rows = await sheet.getRows()
      lists = {
        genres: rows.filter(o => o.Genre !== undefined).map(o => o.Genre),
        sousGenres: rows.filter(o => o.SousGenre !== undefined).map(o => o.SousGenre),
        statuts: rows.filter(o => o.Statut !== undefined).map(o => o.Statut),
        formats: rows.filter(o => o.Format !== undefined).map(o => o.Format),
      }

      initialised = true;
    }
  }

  const getBooks = async () => {
    await init();
    return books;
  };

  const getBook = async (id) => {
    await init();
    return books[id];
  }

  const getBookCount = async () => {
    await init();
    return books.length;
  }

  const saveBook = async (id, book) => {
    await init();
    const data = books[id]
    for (var prop in book)
      data[prop] = book[prop]
    await data.save()
  }

  const addBook = async (book) => {
    try {
      await init();
      const newBook = await bookSheet.addRow(book);
      books = [...books, newBook]
    } catch (error) {
      console.log(error);
    }
  }

  const deleteBook = async (id) => {
    await init();
    await books[id].delete();
    books.splice(id, 1)
    return books;
  }

  const getLists = async () => {
    await init();
    return lists;
  }

  return {
    getBooks,
    getBook,
    getBookCount,
    saveBook,
    addBook,
    deleteBook,
    getLists,
    defaultState,
    defaultList,
  }
}

export default api()
