import { Link, useHistory } from "react-router-dom";
import { useEffect, useState } from "react";
import { Trash } from 'react-bootstrap-icons'
import api from '../api'
import { usePersistedState } from "../utils/usePersistedState";

const Home = () => {
  const [data, setData] = useState();
  const [filter, setFilter] = usePersistedState('filter', null);
  const [filterGenre, setFilterGenre] = usePersistedState('filterGenre', null);
  const [filterSousGenre, setFilterSousGenre] = usePersistedState('filterSousGenre', null);
  const [displayMode, setDisplayMode] = usePersistedState('displayMode', 'table')
  const [lists, setList] = useState(api.defaultList);

  const getData = async () => {
    const books = await api.getBooks()
    setData([...books])
    //setData(Object.keys(data).map((key) => data[key]));
  };

  useEffect(() => {
    const getList = async (ame) => setList(await api.getLists())
    getData();
    getList();
  }, []);

  const history = useHistory()

  const handleDelete = async (e, rowIndex) => {
    e.stopPropagation();
    api.deleteBook(rowIndex)
    data.splice(rowIndex, 1)
    setData([...data])
    // data.splice(rowIndex, 1);
    // setData(data);
    // try {
    //   const res = await fetch(
    //     `https://sheet.best/api/sheets/bff990d0-8ada-43e9-97eb-0ad668bb19ec/${rowIndex}`,
    //     {
    //       method: "DELETE",
    //     }
    //   );
    //   if (res.ok) {
    //     const updatedData = data.filter((_, i) => i !== rowIndex);
    //     setData(updatedData);
    //   }
    // } catch (error) {
    //   console.log(error);
    // }
  };

  const handleFilter = (e) => setFilter(e.target.value)
  const handleClick = (i) => history.push(`/edit/${i}`)
  const handleGenreFilter = (o) => setFilterGenre(o)
  const handleSousGenreFilter = (o) => setFilterSousGenre(o)

  const getFilter = () => filter?.toLowerCase().trim()

  const displayList = data?.filter((item) =>
    (!filter
    || item.Name.toLowerCase().indexOf(getFilter()) >= 0
    || item.Author.toLowerCase().indexOf(getFilter()) >= 0
    || item.Genre.toLowerCase().indexOf(getFilter()) >= 0
    || item.SousGenre.toLowerCase().indexOf(getFilter()) >= 0)
    && (!filterGenre || item.Genre === filterGenre)
    && (!filterSousGenre || item.SousGenre === filterSousGenre))

  return (
    <div>

      {/* mode d'affichage */}
      <div className="mb-3 border rounder bg-light col-sm-2">
        <div className="form-check form-switch">
          <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" checked={displayMode === 'table'}
            onChange={(e) => setDisplayMode(e.target.checked ? 'table' : 'list')}
          />
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault" >
            {displayMode === 'table' ? "Affichage condensé" : "Affichage en liste"}
          </label>

        </div>
      </div>

      {/* Recherche*/}
      <div className="mb-3 row">
        <div className="col-sm-11">
        <input type="text" className="form-control" placeholder="rechercher: un titre, un auteur" value={filter || ''} onChange={handleFilter} autoFocus />
        </div>
        <div className="col-sm-1">
          <span className="badge rounded-pill bg-info" style={{ verticalAlign: 'sub' }}>{`${displayList?.length??0} / ${data?.length??0}`}</span>
        </div>
      </div>

      {/* Mode table */}
      <table className={`table table-hover table-sm align-middle  border rounded bg-light ${displayMode === 'table' ? '' : 'd-none'}`}>
        <colgroup>
            <col className="col-md-5" />
            <col className="col-md-1" />
            <col className="col-md-2" />
            <col className="col-md-2" />
            <col className="col-md-2" />
        </colgroup>
        <thead>
          <tr>
            <th scope="col">Titre</th>
            <th scope="col">&nbsp;</th>
            <th scope="col">Auteur</th>
            <th scope="col">
              <div className="dropdown">
                <div className="dropdown-toggle dropdown-toggle-split" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                  {filterGenre ? <mark>{filterGenre}</mark> : 'Genre'}
                  &nbsp;
                </div>
                <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                  <li><button className="dropdown-item" onClick={() => setFilterGenre(null)}>&nbsp;</button></li>
                  {lists.genres.map(item => (
                    <li key={item}>
                      <button className="dropdown-item" onClick={() => handleGenreFilter(item)}>{item}</button>
                    </li>))}
                </ul>
              </div>
            </th>
            <th scope="col">
            <div className="dropdown">
                <div className="dropdown-toggle dropdown-toggle-split" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                {filterSousGenre ? <mark>{filterSousGenre}</mark> : 'Sous genre'}
                  &nbsp;
                </div>
                <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                  <li><button className="dropdown-item" onClick={() => setFilterSousGenre(null)}>&nbsp;</button></li>
                  {lists.sousGenres.map(item => (
                    <li key={item}>
                      <button className="dropdown-item" onClick={() => handleSousGenreFilter(item)}>{item}</button>
                    </li>))}
                </ul>
              </div>

            </th>
          </tr>
        </thead>
        <tbody>
          {displayMode === 'table' && displayList?.map((item, i) => (
            <tr key={i} onClick={() => handleClick(item._rowNumber - 2)}>
              <th scope="row">
              <img
                  src={item.Jacket || '/MaLibrairie/logo192.png'}
                  height="50" width="30" alt='no-pic' ></img>
                &nbsp;&nbsp;
                {item.Name}
                </th>
              <td>{item.Tome ? ('tome ' + item.Tome) : ('')}</td>
              <td>{item.Author}</td>
              <td>{item.Genre}</td>
              <td>                
                <div style={{ position: 'relative' }}>
                <div>{item.SousGenre || '-'}</div>
                <div style={{ position: 'absolute', bottom: 0, right: 0 }}>
                  <Trash 
                    color="gray" 
                    size={16}
                    className="delete-icon"
                    onClick={(e) => handleDelete(e, item._rowNumber - 2)} />
                </div>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* mode Liste */}
      <div className={`accordion ${displayMode === 'table' ? 'd-none' : ''}`} id="accordionExample">
        {displayMode !== 'table' && displayList?.map((item, i) => (
          <div className="accordion-item" key={i}>
            <h2 className="accordion-header" id={`heading${i}`}>
              <button
                className="accordion-button"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target={`#collapse${i}`}
                aria-expanded="true"
                aria-controls={`collapse${i}`}
              >
                <img
                  src={item.Jacket || 'https://img.freepik.com/premium-vector/book-cover-transparent-background-also-includes_270215-78.jpg?w=740'}
                  height="50" width="30" alt='no-pic' ></img> {item.Name} {item.Author}
              </button>
            </h2>
            <div
              id={`collapse${i}`}
              className="accordion-collapse collapse"
              aria-labelledby={`heading${i}`}
              data-bs-parent="#accordionExample"
            >
              <div className="accordion-body">
                <div className="d-flex justify-content-between align-items-center">
                  <span>
                    <strong className="display-6">{item.Name}</strong> ---{" "}
                    {item.Author}

                  </span>
                  <span>
                    <Link to={`/edit/${i}`} style={{ textDecoration: "none" }}>
                      Edit
                    </Link>
                    <button
                      className="btn btn-sm btn-danger ms-1"
                      onClick={() => handleDelete(i)}
                    >
                      X
                    </button>
                  </span>
                </div>
                <p>{item.Description}</p>
                <img src={item.Jacket} alt={item.name} ></img>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/** message aucun livre */}
      {(displayList?.length === 0)
        ? (<h4 className="col-12 text-muted text-center">Aucun livre ne corresponds à votre recherche</h4>)
        : ('')}

      {/** footer */}
      <div>&nbsp;</div>

    </div>
  );
};

export default Home;
