import { useState, useEffect } from 'react'

const StarRating = ({ value, onChange }) => {
  const [rating, setRating] = useState(0)
  const [hover, setHover] = useState(0);
  const [hoverClear, setHoverClear] = useState(false);

  useEffect(() => {
    setRating(value);
  }, [value])

  return (
    <div className="star-rating">
      {[...Array(5)].map((star, index) => {
        index += 1;
        return (
          <button
            type="button"
            key={index}
            className={`starButton  ${index <= (hover || rating) ? "on" : "off"}`}
            onClick={() => onChange(index)}
            onMouseEnter={() => setHover(index)}
            onMouseLeave={() => setHover(rating)}
          >
            <span className="star">&#9733;</span>
          </button>
        );
      })}
      <button 
        type="button"
        className={`clearButton ${hoverClear ? "on" : "off"}`}
        onMouseEnter={() => setHoverClear(true)}
        onClick={() => onChange(0)}
        onMouseLeave={() => setHoverClear(false)}

        >
          x</button>
    </div>
  );
};

const Input = ({ label, value, className, labelWidth, valueWidth, autofocus = false, onChange }) => {
  return (
    <div className={`${className}`}>
      <label htmlFor="Name" className={`col-sm-${labelWidth ?? 12} col-form-label`} >
        {label}
      </label>
      <div className={`col-sm-${valueWidth ?? 12}`}>
        <input
          type="text"
          className="form-control"
          name="Name"
          value={value}
          onChange={(e) => onChange(e.target.value)}
          autoFocus={autofocus}
        />
      </div>
    </div>)
}

const InputList = ({ label, value, className, labelWidth, valueWidth, list, onChange }) => {
  return (
    <div className={`${className}`}>
      <label htmlFor="Name" className={`col-sm-${labelWidth ?? 12} col-form-label`} >
        {label}
      </label>
      <div className={`col-sm-${valueWidth ?? 12}`}>
        <select
          type="text"
          className="form-select"
          name="Name"
          value={value || ''}
          onChange={(e) => onChange(e.target.value)}
        >
          <option value="">-</option>
          {list.map(item => (<option key={item} value={item}>{item}</option>))}
        </select>
      </div>
    </div>)
}

const InputCheckList = ({ label, value, className, labelWidth, valueWidth, list, onChange }) => {
  return (
    <div className={`${className}`}>
      <label htmlFor="Name" className={`col-sm-${labelWidth ?? 12} col-form-label`} >
        {label}
      </label>
      <div className={`col-sm-${valueWidth ?? 12}`} style={{ paddingTop: 10 }}>
        {list.map(item => (
          <div className="form-control-check form-check form-check-inline" key={item}>
            <input className="form-check-input" type="radio"
              name="Format"
              id={'radio-' + item}
              value={item}
              checked={value === item}
              onChange={(e) => onChange(e.target.value)} />
            <label className="form-check-label" htmlFor={'radio-' + item}>
              {item}
            </label>
          </div>
        ))}
      </div>
    </div>)
}

